export default {
  model: {
    prop: 'value',
    event: 'input'
  },
  props: {
    value: Boolean,
    confirmButtonText: {
      type: String,
      default: ''
    },
    cancelButtonText: {
      type: String,
      default: ''
    },
  },
  methods: {
    onClose() {
      this.$emit('input', false)
      this.$emit('cancel', false)
    },
    onConfirm() {
      this.$emit('confirm')
    },
  }
}